<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    Św. Innocenty Canoura<br />(1887 - 1934)
                </h1>
                <img src="../assets/images/saints/innocenty-canoura.jpg" alt="">
                <p>Jest męczennikiem XX wieku, ponieważ zginął z rąk oprawców rewolucyjnych w czasie wojny domowej w Hiszpanii. Jego ojczyzną była Galicja hiszpańska, tereny północne półwyspu Iberyjskiego. Urodził się jako słabe dziecko, miał na imię Manuele. Przez 15 lat przebywał w domu rodzinnym. Jego rodzina cieszyła się pobożnością i uczynnością.  Kiedyś zdarzyło się ,że w okolicy misje parafialne prowadzili misjonarze z zakonu Pasjonistów. Wtenczas młody manuele zgłosił się do klasztoru w Mondonedo a potem do San Sebastian. Miał braki w wykształceniu więc skierowano go do szkoły średniej a po niej w końcu rozpoczął nowicjat. W tym czasie otrzymał sakrament bierzmowania i złożył pierwsze śluby.  Studiował następnie teologię i otrzymał święcenia kapłańskie  w 1913  roku. Ale nie łatwo przychodziła mu nauka! Nadrabiał wiele zaległości, miał wadę wymowy, ale praca dała efekty. Wyprzedził nawet innych. Po święceniach sam stał się wykładowcą teologii i filozofii, literatury. Doceniał naukę i studiowanie.  Był dobrym wykładowcą. Jak sam napisał do siostrzeńca: „nieporównanie więcej wart jest młody człowiek , który zdobył dobre wykształcenie, niż ten, który jest dobrze sytuowany. Pieniądze bowiem można łatwo roztrwonić na tysiące sposobów, wiedza zaś trwa tak długo, jak długo żyje ten, który ją posiada”.  W 1926 r. Po przeniesieniu do Mieres zajął się pracą nauczycielską, misyjną i rekolekcyjną.  Zetknął się wtedy z problemami społecznymi ówczesnej Hiszpanii. Z czasem sytuacja w kraju się zaogniła i  doszło do rozruchów i wzajemnej walki pomiędzy lewicą i prawicą. Ofiarą tych walk stali się księża, duchowieństwo jak i ludność cywilna.  Kiedy 4 października 1934 r. O. Innocenty udał się z Mieres do Turon aby spowiadać w pierwszy piątek miesiąca w Kolegium braci Szkolnych, nie przypuszczał, że wkrótce poniesie śmierć męczeńską. Po mszy św. zebrano kapłanów i braci, zaprowadzono do siedziby rewolucjonistów. W celi  panował nastrój modlitwy i wzajemnej życzliwości. Miejscowa ludność starała się przekonać władze, że dobrzy nauczyciele potrzebni są w każdym ustroju, ale bezskutecznie.  Około północy wyprowadzono pasjonistę i 8 braci Szkolnych , zawieziono na cmentarz, do przygotowanych dołów. Następnie rozstrzelano nad krawędzią i zakopano .  Pasjonista Innocenty umierał z przekonaniem że spotka Chrystusa, zjednoczy się z Ukrzyżowanym, zapłacił miłością do niego, za Miłość.</p><p>Męczenników z Touron – O. Innocentego i Braci Szkolnych beatyfikował w 1990 r. i kanonizował w 1999 r. papież Jan Paweł II.</p>
            </div>
        </div>
    </div>
</template>

