<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    26 Błogosławionych Męczenników z Daimiel<br />
                    ( + 1936)<br />męczennicy hiszpańscy
                </h1>
                <img src="../assets/images/saints/meczennicy.jpg" alt="">
                <p>Tym razem nie opisujemy historii powołania jednego Pasjonisty, ponieważ „bohaterami” tego opisu jest grupa Pasjonistów  - męczenników hiszpańskich.</p>
                    <p>
                        W 1931 r. do władzy w Hiszpanii doszedł republikański rząd, będący pod silnym wpływem rewolucji październikowej w Rosji. W wyniku reakcji przeciw tym rządom wybuchła wojna domowa, co pociągnęło za sobą wiele ofiar, także wśród osób duchownych. Wspólnota Pasjonistów z Daimiel stała się także ofiarą tych prześladowań. Zakonnicy nie angażowali się po żadnej stronie, ale zostali wciągnięci w wir prześladowań religijnych.
                    </p>
                <p>
                    Piętnastu z nich było młodymi scholastykami (studentami), w wieku 18 do 21 lat, którzy przygotowywali się do wyjazdu na odległe misje w Ameryce Łacińskiej. Towarzyszyli im profesorowie i bracia, którzy kierowi ich formacją.
                </p>
                <p>
                    Nocą z 21 na 22 lipca zostali wyrzuceni z klasztoru prze 200 uzbrojonych żołnierzy. Przewodniczył zakonnikom o. Prowincjał Nicefor. Zgromadzeni w kościele, odbyli ostatnią spowiedź, a prowincjał udzielił im wiatyku.
                </p>
                <p>
                    Następnie żołnierze przepędzili zakonników w środku nocy, podzielili na małe grupki a następnie uśmierceni. W różnym czasie i w różnych miejscach zostali zmasakrowani z powodu jednej "zbrodni": pozostali wiernymi swemu powołaniu zakonnemu. Prowincjał i 14 innych zostali zabici 23 lipca, trzech innych 25 lipca, dwóch innych 25 września i sześciu innych 23 października. Liczni spośród nich przeczuwali, iż wkrótce będą "ukrzyżowani" z Chrystusem i perspektywa męczeństwa zalewała ich dusze radością i pragnieniem dzielenia losu Jezusa. Wielu padało z krzyżem w dłoni, krzycząc: "Niech żyje Chrystus Król". Pytano jednego śmiertelnie rannego scholastyka, czy bardzo cierpi? "Tak - odrzekł - ale jestem przecież Pasjonista!" Umierali za swój naród z uśmiechem, przebaczając swoim katom i modląc się o pojednanie narodowe. Dnia 1 października 1989 roku Jan Paweł II dokonał beatyfikacji 26 Pasjonistów z klasztoru w Daimiel.
                </p>
                <p>
                    <i>
                        Obywatele Kalwarii, oto przyszło nasze Getsemani! Nasza natura ludzka w słabej swej części ugina się i lęka w perspektywie Kalwarii. Tak jak człowieczeństwo Jezusa, jest przerażona i powalona. Lecz Jezus Chrystus jest z nami. Daję wam tego, który jest siłą słabych. Anioł umacniał Jezusa. Nas zaś podtrzymuje sam Jezus. (...)Obywatele Kalwarii, odwagi! Trzeba nam, umrzeć za Chrystusa.
                    </i>
                    (Słowa o. Nicefora skierowane do współbraci przed przyjęciem wiatyku).
                </p>

            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Martyrs"
    }
</script>

<style scoped lang="scss">
    @import "../assets/scss/style";

</style>


