ał wówczas 19 lat, kiedy pojawił się w klasztorze na Monte Argentario, w pierwszym klasztorze Pasjonistów, tam włożył habit zakonny w dniu 14. Listopada 1801 roku. Czy były wahania? Tak! Młody Wawrzyniec Salvi przed rozpoczęciem nowicjatu odczuwał pokusy rezygnacji. Przychodziły myśli o tym, że w zakonie będzie bardzo nieszczęśliwy.  Pokusa była tak mocna, że się załamał. Wyjawił z płaczem tajemnicę mistrzowi nowicjatu, a ten obdarzył go pociechą, taką, że  doznał uspokojenia i radości.  \"Pokusę pokonałeś, ponieważ ją ujawniłeś\", powiedział mistrz.  20 listopada 1802 r.  Wawrzyniec złożył pierwsze śluby zakonne , a także sporządził  \"Akt oddania się Jezusowi i Maryi Jego Matce\" Był czcicielem Maryi, dlatego też przed rozpoczęciem nowicjatu, nie zmienił , tak jak wszyscy, swego imienia, ale dodał do swego imię Maria. Wawrzyniec Maria  po ukończeniu studiów teologicznych w Rzymie otrzymał święcenia kapłańskie w 1805 r, a potem specjalizował się w kaznodziejstwie pod kierunkiem św.  Wincentego Strambi. ")]),_vm._v(" "),_c('p',[_vm._v("\n                Potem przyszły trudne czasu dla zakonów. Napoleon wydał dekret zmuszający zakonników i księży do składania przysięgi  władzy okupacyjnej. Wielu z nich tego nie chciało uczynić, tak było z młodym Wawrzyńcem. Przebywając w ukrycie w jednym z klasztorów pełnił rolę nauczyciela świeckiego w małej miejscowości koło Loreto.  Nastał rok 1814, reorganizacja życia zakonnego, zatwierdzanie ponowne zakonów przez papieża, jednym z pierwszych byli Pasjoniści. Wawrzyniec Maria Salvi rozpoczął swą działalność jako misjonarz, rekolekcjonista i kaznodzieja. Szczególną czcią darzył Dzieciątko Jezus. Dlaczego jako Pasjonista darzył szczególną adoracją Dzieciątko Jezus? Ponieważ , gdy zachorował śmiertelnie, ukazało mu się Dzieciątko Jezus, które przywróciło mu zdrowie i poleciło, aby rozpowszechniał nabożeństwo do Jego Dzieciństwa. Odtąd wiele głosił kazań o dzieciątku a także sam woził ze sobą figurkę Dzieciątka Jezus, istnieją dowody, że poprze ten wizerunek dokonywał cudów. Został słynnym misjonarzem , kaznodzieją, kierownikiem duchowym, nawet kardynałów.  Był zawsze ruchliwy, gorliwym spowiednikiem, prowadził liczną korespondencje ze swymi penitentami, . traktował czas jako dar Boży. Zawsze zachowywał gorliwość w modlitwach zakonnych, rozmyślaniu pomimo wiele podróży, prac apostolskich, misji i rekolekcji. Był przełożonym klasztoru oraz konsul torem prowincjalnym.Za  przyczyną jego modlitwy dokonywały się uzdrowienia, a także ustąpienie epidemii.  Zmarł w opinii świętości  w drodze d posługi duszpasterskiej w jednym z domów przyjaciół zakonu  12 czerwca 1856. O wielkiej czci do jego osoby świadczy fakt , iż w momencie jego śmierci ludzie  ogołocili jego szczątki ,  zabierając różne rzeczy  ( kawałki habitu, włosy, krzyż, różaniec, figurkę Dzieciątka Jezus) traktując je jako święte relikwie.   Po jego śmierci odnotowywano kolejne  uzdrowienia za jego wstawiennictwem  Jan Paweł II  dokonał beatyfikacji Wawrzyńca Marii Salvi dnia 1 października 1989 roku.\n            ")])])])])}]
