<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    Św. Wincenty Maria Strambi<br/>(1745 - 1824)
                </h1>
                <img src="../assets/images/saints/vincenty-strambi.jpg" alt="">
                <p>Został Pasjonistą będąc już księdzem diecezjalnym. Jak do tego doszło?
                    Od małego pociągał go ołtarz i kościół. Może już jako mały chłopak przyjmując I Komunię Świętą w
                    sercu sobie postanowił by zostać księdzem. Kiedy potem dowiedział się o tym jego ojciec, wtedy
                    stanowczo się sprzeciwił. Wincenty był jedynym synem włoskiego kupca, który liczył na pomoc z jego
                    strony. Chciał by został spadkobiercą sporego majątku. Jednak Wincenty rozpoczął studia teologiczne,
                    podczas których przyjmował poszczególne stopnie niższych święceń. Jeszcze tuż przed przyjęciem
                    święceń wyższych ojciec zaproponował mu dobrą partię, aby mógł się ożenić, ale Wincenty wskazał na
                    Maryję i powiedział, że to jest jego Oblubienica. Ojciec ustąpił, marząc jednak o karierze
                    kościelnej dla swego syna. W roku 1767 Wincenty Strambi musi przygotować się do przyjęcia święceń
                    kapłańskich, dlatego odprawia specjalne rekolekcje akurat w klasztorze Pasjonistów na Monte
                    Fogliano. Tam spotkał Założyciela Zgromadzenia, już 73-letniego Pawła od Krzyża. Wincenty poczuł się
                    porwany od pierwszego spotkania ze świętym. Co spowodowało ten nagły zwrot? Co pociągało
                    seminarzystę do tego, by włączyć do życia pokutniczego i surowego? Tego do końca nie wiemy. Prosi
                    Świętego o przyjęcie. Po jakimś czasie Paweł Daneo przystaje na prośbę, ale odkłada na przyszły
                    czas. Wincenty zostaje wyświęcony na kapłana, odprawia Mszę św. prymicyjną w rodzinnym mieście. A
                    tym czasem jego ojciec nie ustępuje, widzi go jako księdza diecezjalnego. Wincenty dalej pogłębia
                    swe studia i wciąż pragnie przywdziać habit zakonny w zakonie Pasjonistów. Ucieka z domu i udaje się
                    do nowicjatu , przywdziewa po 3 dniach habit stawiając rodziców przed faktem dokonanym. To oburza
                    ojca, który czyni kolejne kroki, aby temu przeszkodzić. Co robi? Pisze skargę do św. Pawła od Krzyża
                    i żąda oddania syna! Potem z kolei ucieka się do protekcji u kardynała Oddi, który dla próby wysyła
                    innego księdza z poleceniem młodego Strambiego. Młody ksiądz Strambi jednak tak gorliwie przedstawił
                    piękno życia zakonnego wysłannikowi kardynała, że mało co a ten nie wstąpiłby również do
                    Pasjonistów!
                </p>
                <p>
                    Ojciec Wincentego poddał się woli Bożej. W 1769 r. Wincenty Maria Strambi złożył śluby. W zakonie
                    okazał się wielkim misjonarzem, przełożonym klasztorów, Prowincji, aż w końcu w 1801 r. Otrzymuje
                    nominację na biskupa diecezji Tolentino i Maceraty w Italii. Był biskupem niezwykłym. Jako zakonnik
                    ślubował ubóstwo i jako biskup dalej je przestrzegał, wspomagając innych ubogich, biednych . stał
                    się prawdziwym ojcem ubogich, a jednocześnie człowiekiem modlitwy. Pod koniec swego życia, mając 78
                    lat usługuje w Pałacu Kwirynalskim papieżowi Leonowi XII. Gdy ten ciężko zachoruje, wtedy składa
                    Bogu swe życie jako ofiarę w zamian za życie papieża. Rzeczywiście, po kilku dniach papież
                    zdrowieje, a bp Strambi, Pasjonista umiera. Pochowany został w pobliżu Ojca Założyciela w Bazylice
                    świętych. Jana i Pawła w Rzymie.
                </p>
                <p>
                    W 1957 ciało biskupa przeniesiono do Katedry św. Juliana w Maceracie. Beatyfikował go 26 kwietnia
                    1925 papież Pius XI, kanonizował 11 czerwca 1950 Pius XII. Wspomnienie liturgiczne na terenie Włoch
                    obchodzone jest 1 stycznia. W kalendarzu własnym zgromadzenia pasjonistów wspomnienie obchodzone
                    jest 24 września.
                </p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "WincentyMariaStrambi"
    }
</script>

