mności w\n                bogactwie i przygodach? Tu jest prawdziwe niebo na ziemi. Nazajutrz przy odjeździe powiedział\n                furtianowi \"Ten dom jest prawdziwym rajem na ziemi!\".")]),_vm._v(" "),_c('p',[_vm._v("\n                W 1853 r. został beatyfikowany Założyciel Pasjonistów, młody Cezary bardzo Odprawił rekolekcje w\n                rzymskim klasztorze Pasjonistów, podczas których podjął decyzję wstąpienia do tego zakonu. Rodzice\n                byli jednak przeciwni. przeżył to wydarzenie. Cezary przedstawił świadectwo moralności i został\n                jednak przyjęty. Rozpoczął nowicjat w domu na Monte Argentario. Przyjął habit zakonny, ale już\n                wkrótce zaczął chorować. Zbyt przesadnie potraktował praktyki zakonne, dlatego musiał opuścić\n                nowicjat. Zwrócił się jednak do Generała zakonu by mógł dalej przebywać w murach klasztoru i\n                studiując teologię ,przygotowywać się do kapłaństwa. Pozwolono mu włożyć sutannę.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Kiedy został kapłanem , musiał zdecydować co dalej? Albo ponownie wstąpić do zakonu, albo przejść do\n                diecezji. Wybrał to pierwsze, za radą biskupa poprosił ponownie o przyjęcie do Pasjonistów. Wiosną\n                1856 r . Cezary Silvestrelli przyjął ponownie habit zakonny i na zawsze już został Pasjonistą. W\n                nowicjacie odznaczał się szczególną świętością, miał za współbrata młodego Gabriela Possenti, który\n                potem w wieku 24 lat został świętym.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Sam Cezary, już bernard Silvestrelli po nowicjacie uzupełnił studia, został dyrektorem klerykatu,\n                pełnił różne ważne funkcje w zakonie , aż w końcu przez 5 kadencji wybierany był na Generała zakonu.\n                Za jego kadencji zakon rozszerzył swą działalność: liczba zakonników wzrosła dwukrotnie a klasztory\n                Pasjonistów powstawały w krajach europejskich i w Ameryce. Papież starał się mianować go kardynałem, ale ten w pokorze tej funkcji nie chciał przyjąć.\n                Bernard Silvestrelli długoletni Generał i krzewiciel zakonu zmarł w 1911 r. w klasztorze w Moricone.\n                Został beatyfikowany przez Jana Pawła II w 1988 r.\n            ")])])])])}]
