<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    Historia Zgromadzenia
                </h1>
                <p>
                    Zgromadzenie Męki Jezusa Chrystusa, Pasjoniści, przedtem nazywane Zgromadzeniem Kleryków Bosych
                    Najświętszego Krzyża i Męki Pana Naszego Jezusa Chrystusa, zostało założone w wieku XVIII przez
                    włoskiego mistyka Św. Pawla od Krzyża, żyjącego w latach 1694-1775. Paweł Daneo pochodzący z licznej
                    rodziny kupieckiej w Ovadzie jako młody człowiek odczuł ducha krucjaty i zaangażowania się w wojnę
                    przeciwko Turkom, jednakże stało się dla niego jasne to, że jego powołaniem nie jest obrona wiary
                    orężem.
                </p>
                <p>
                    W dalszych latach doznał wewnętrznych natchnień, które dotyczyły jego osoby (samotność i życie w
                    pokucie) i wspólnoty (założenie zgromadzenia). W 1720 r. otrzymał wewnętrzną wizję znaku Męki
                    Pańskiej i stroju zakonnego. Uświadomił sobie, że ma nosić taki habit żałobny na pamiątkę Męki
                    Naszego Pana oraz ożywiać "wdzięczną pamiątkę o niej w duszach wiernych". Charyzmatem nowego
                    zgromadzenia miały być więc samotność, ubóstwo, pokuta oraz kult Męki Pańskiej, rozumiany jako
                    największy wyraz inicjatywy miłosnej Boga. Wsparty duchowo przez biskupa F.A. di Gattinara,
                    rozpoczął dzieło zakładania nowej wspólnoty, odbył 40 - dniowe rekolekcje, notując swe przeżycia
                    mistyczne oraz pisząc w tym czasie Regułę.
                </p>
                <p>
                    W 1721 r. próbował otrzymać audiencję u papieża, lecz jego wizyta skończyła się tylko odnowieniem
                    ślubu wypełnienia charyzmatu i szerzenia pamięci i kultu Męki Pańskiej. Zdecydowany był też na
                    przyjmowanie innych towarzyszy do swej wspólnoty. Pierwszą samotnią przyjętą od bp. Gattinara było
                    Monte Argentario nad Morzem Adriatyckim. W roku 1725 poznał osoby, które stały się jego protektorami
                    w Rzymie. Papież Benedykt XIII zachęcił go do tworzenia grupy ustnie aprobując nowy instytut. Św.
                    Paweł od Krzyża pracował w szpitalu w Rzymie i tam również przyjął święcenia kapłańskie. Dnia 15
                    maja 1741 r. została zatwierdzona pierwsza Reguła nowego instytutu, pod nazwą Zgromadzenie Ubogich
                    Kleryków Bosych pod wezwaniem Najświętszego Krzyża i Męki Jezusa Chrystusa. W lipcu tego roku
                    pierwszych sześciu towarzyszy złożyło trzy śluby zakonne dodając czwarty ślub szerzenia nabożeństwa
                    i kultu Męki Chrystusa przez medytację i nauczanie. W 1769 r. papież Klemens XI zatwierdził
                    zgromadzenie zakonne ze ślubami prostymi i przywilejami przysługującymi zakonom żebrzącym. Paweł
                    Daneo po otrzymaniu duchowego oświecenia na początku swej drogi przez prawie 40 lat pozostawał w
                    nocy duchowej, która została mu odjęta pod koniec życia.
                </p>
                <p>
                    W chwili śmierci Pawła od Krzyża instytut liczył sobie 176 zakonników i 12 klasztorów. Do połowy XIX
                    wieku instytut działał we Włoszech. Potem klasztory zostały założone w Belgii, Anglii, USA,
                    Hiszpanii , następnie w Ameryce Południowej, Australii i Francji. Pierwsze zagraniczne fundacje
                    istniały jednak w Walii i Bułgarii od 1782 roku pod bezpośrednim zwierzchnictwem Kongregacji d/s
                    Krzewienia Wiary. W wieku XX Zgromadzenie zostało przeszczepione m.in. do Irlandii, Palestyny,
                    Polski i Niemiec.
                </p>
                <p class="center">
                    <strong>
                        STRONA OFICJALNA ZGROMADZENIA PASJONISTÓW:
                    </strong>
                </p>
                <p class="center">
                    <span onclick="navigator.app.loadUrl('http://www.passiochristi.org/', {openExternal : true});">www.passiochristi.org</span>
                </p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "History"
    }
</script>

<style scoped lang="scss">
    @import "../assets/scss/style";

</style>



