<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    Św. Maria Goretti<br />
                    (1890 - 1902)
                </h1>
                <img src="../assets/images/saints/maria-goretti.jpg" alt="">
                <p>Nazwana przez papieża Piusa XII „św. Agnieszką XX wieku”, urodziła się na wsi pod Ankoną we Włoszech w roku 1890, jako trzecia z siedmiorga dzieci.</p>
                    <p>
                        Była, jak określiła jej matka, dziewczynką „szczęśliwą, dobrą, szczerą i uczuciową, bez kaprysów, poważną i zawsze posłuszną”.
                    </p>
                <p>
                    Jej ojciec umarł, gdy miała dziewięć lat. Maria opiekowała się młodszym rodzeństwem, pomagała w pracach domowych, podczas, gdy jej matka była zajęta przy gospodarstwie.
                </p>
                <p>
                    Pierwszą Komunię św. Maria przyjęła ukończywszy jedenaście lat i odtąd ze wszystkich sił dążyła, by co dzień być lepszą.
                </p>
                <p>
                    Sześć miesięcy później bohaterska dziewczynka została poddana ciężkiej próbie.
                </p>
                <p>
                    Rodzina Goretti dzieliła dom ze wspólnikami ojca Marii, Aleksandrem, niegodziwym młodzieńcem, który zaczął prześladować dziewczynkę swą grzeszną namiętnością.
                </p>
                <p>
                    Odepchnęła go natychmiast, ale nie poskarżyła się nikomu, gdyż Aleksander zagroził, że zabije ją i jej matkę, jeśli go wyda.
                </p>
                <p>
                    Nie zrezygnował jednak ze swego pożądania i drugi raz gwałtownie zaatakował Marię. Święta znowu mu się oparła ze wszystkich sił krzycząc: „Nie, to jest grzech. Bóg nie chce tego”.
                </p>
                <p>
                    Aleksander, zdjęty strachem i gniewem, zaczął uderzać ją na oślep sztyletem. Czternaście ciosów przeszyło jej ciało.
                </p>
                    <p>
                        Maria została przewieziona do szpitala w Nettuno. Chirurdzy pracowali gorączkowo nad uratowaniem jej życia, ale wkrótce okazało się, że nic nie można zrobić. Następnego ranka, przyjęła Komunię św.
                    </p>
                <p>
                    Zapytana o Aleksandra, odpowiedziała wyraźnie, że mu przebaczyła, że będzie się za niego modliła i że chciałaby go zobaczyć w niebie. 6 lipca 1902 roku dziewczynka umarła. 27 lipca 1950 roku ogłoszono ją świętą.
                </p>
                <p>
                    Po raz pierwszy w historii Kościoła działo się to w obecności rodziny kanonizowanej. Modlitwy Marii w intencji jej mordercy zostały wysłuchane. W ósmym roku pobytu w więzieniu Aleksander całkowicie się odmienił.
                </p>
                <p>
                    Zwolniony po dwudziestu siedmiu latach za dobre sprawowanie, pospieszył matkę świętej błagać o przebaczenie.
                </p>
                <p>
                    Potem wstąpił do zakonu. Był świadkiem w procesie kanonizacyjnym Marii. Dane mu było również uczestniczyć w ceremonii jej kanonizacji.
                </p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "MariaGoretti"
    }
</script>

<style scoped lang="scss">
    @import "../assets/scss/style";

</style>


