<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    Pasjoniści w Polsce
                </h1>
               <p>
                   Założyciel od początku marzył o tym, aby przeszczepić zakon na tereny polskie, do czego jednak za jego życia nie doszło z powodów politycznych. Pierwszy rozbiór Rzeczpospolitej oraz nieprzyjazne gesty zaborców wobec Kościoła Katolickiego nie sprzyjały tej idei. Parę lat po śmierci Pawła Daneo, próby tej chciał dokonać w 1781 r. wielki referendarz litewski Wincenty hr. Tyszkiewicz, pragnął ofiarować Pasjonistom klasztor w Świsłoczy, w diecezji wileńskiej. Przy aprobacie miejscowego ordynariusza oraz Sejmu, zakon jednak nie otrzymał zgody samego króla Polski, co było warunkiem sine qua non ze strony ówczesnego generała w Rzymie na przybycie zakonu do Polski. Kolejne nieudane próby podejmowane były w latach 1913/1914 w Galicji. Główny udział w tym dziele miał o. Jan Konstanty Giecewicz, obywatel cesarstwa Austro-Węgier, w zakonie Zmartwychwstańców pełniący funkcje konsultora lub prokuratora, potem przeniesiony do Pasjonistów lecz z niepowodzeniem. Ostatecznie pozostał w swym dawnym Zgromadzeniu. Jego osoba była przedmiotem zainteresowania papieża Piusa X, który polecał Generałowi Pasjonistów przyjęcie go do zgromadzenia z myślą przeszczepienia zakonu na tereny polskie. Ksiądz Giecewicz pragnął , by pierwszy klasztor powstał na terenie zaboru austriackiego, a konkretnie na Morawach, w diecezji Brno, w zamku Frin , należącym do Adama Stadnickiego, krewnego bp A. Sapiehy. Listy Giecewicza pisane do Generała Pasjonistów świadczą o pragnieniu zaproszenia instytutu do siebie. Poparty był w tej idei również głosem biskupa diecezji Brno, Pawła Hunga oraz innych biskupów , także polskich. Jednakże, mimo zachęt i pragnień hr. Stadnicki postawił pewne warunki pobytu zakonu na zamku. Brak stałego zabezpieczenia warunków mieszkaniowych i konieczność podjęcia duszpasterstwa parafialnego przyczyniły się do odmowy ze strony władz zakonnych w Rzymie. Dalsze starania ks. Gieczewicza skoncentrowały się na próbach założenia klasztoru w Polsce tak, aby można było uniknąć fundacji o charakterze niemieckim i uniknąć antagonizmów narodowych. W tym czasie ks. abp. A. Sapieha poinformował go o kilku ochotnikach z zaboru rosyjskiego, którzy chcieliby wstąpić do Pasjonistów. Starania J.K.Giecewicza w końcu Skippy się na fundacji, która byłaby położona na pograniczu wszystkich zaborów z możliwością rozszerzania jego działalności przez wzbudzanie powołań na wszystkich ziemiach polskich. Pełen zapału i entuzjazmu spotkał się jednak z trudnościami politycznymi a także z pasywną postawą ówczesnego Generała, tym bardziej, iż był to koniec kadencji władz w Rzymie, które nie chciały wiązać Zgromadzenia pochopnymi zobowiązaniami przed kolejną1 Kapitułą Generalną. Ks. Giecewicz przyznaje w liście do przełożonych Zgromadzenia, że winę za fiasko przeszczepienia Pasjonistów na ziemie polskie bierze na siebie. W 1914 r. wprawdzie zachęcany jeszcze przez nowego Generała, nie czuje się na siłach do dalszych działań w tym dziele.
               </p>
                <p>
                    Już przed pierwszą wojną światową do Zgromadzenia Pasjonistów wstępowali pierwsi Polacy tacy jak: o. A.Urbanowicz, o.L.Dowal, o.G. Piegża, o.J.Dzidowski, br.K.Staszewski, br.W. Kamieński. Niektórzy z nich przybyli do Polski aby przeszczepić instytut do ojczyzny po odzyskaniu przez nią niepodległości. Wraz z wyborem kard. A. Rattiego na papieża, który wcześniej był nuncjuszem na terenie Polski, pojawiła się większa szansa na obecność Zgromadzenia w Polsce. Po rozmowach o. Generała z papieżem, który "wyraził gorące pragnienie" przybycia Zgromadzenia do Polski oraz po spotkaniu z polskimi biskupami, doszło do długiego procesu rozmów i propozycji pomiędzy Zgromadzeniem, poszczególnymi biskupami w Polsce oraz samym papieżem. Prym w rozmowach wiedli arcybiskup Julian Nowowiejski z Płocka, pomocniczy bp Stanisław Gall z Warszawy , bp Łoziński z Kielc oraz bp Zygmunt Łoziński z Pińska. Ojciec Generał po odnalezieniu Pasjonistów polskiego pochodzenia, zdecydował się wysłać na rekonesans dwóch zakonników , o. Juliusza od Najśw. Serca Maryi (Jan Dzidowski) oraz brata Kazimierza od Matki Bożej Bolesnej (Kajetan Staszewski), Emisariusze po audiencji u Piusa XI i u kardynała Gaspariiego, sekretarza stanu, od którego otrzymali list polecający dla Nuncjusza Apostolskiego w Warszawie, Wawrzyńca Lauri , przybyli do Polski w kwietniu 1923 roku.
                </p>
                <p>
                    Zakonnicy podróżowali po terenach polskich i odwiedzali proponowane miejsca. Oferowano więc do wyboru klasztor i kościół poaugustiański w Rawie Mazowieckiej, pobernardyński w Łowiczu, pobernardyński klasztor w Przasnyszu lub w Ostrołęce, także w Książu Wielkim, pofranciszkański w Chęcinach, na Grodzisku pod Ojcowem w parafii Skała, pobernardyński klasztor na Karczówce pod Kielcami, pobazyliański w Pińsku, a także opactwo pocysterskie w Jędrzejowie i pobernardyński klasztor w Pińczowie. Szczególne starania w tym względzie podejmował abp płocki, który nawiązał w tym celu bezpośredni kontakt z Ojcem Świętym i o. Generałem. Ostatecznie więc Pasjoniści zdecydowali się na miejscowość Przasnysz w diecezji płockiej. Klasztor bernardyński, opuszczony po upadku Powstania Styczniowego i częściowo zniszczony po działaniach wojennych, został przekazany "na wieczne używanie" pismem z dnia 13 czerwca 1923 roku, wydanym przez abpa Nowowiejskiego. Dnia 17 czerwca nastąpiło przejęcie klasztoru przez Pasjonistów w obecności konsultora generalnego przybyłego z Włoch. Nieobecny na tych uroczystościach abp Nowowiejski zwrócił się do Pasjonistów, kapłanów i ludności Przasnysza w liście, w którym zachęcał między innymi do wsparcia zakonników, korzystania z ich posługi, oraz udzielał błogosławieństwa: "Rośnijcie w liczbę, zdobywając sobie jak najwięcej powołań, pracujcie, a owoc prac waszych niech będzie setny".
                </p>
                <p>
                    Ostatecznie w roku 1923 o. Juliusz Dzidowski oraz brat Kazimierz Staszewski wraz z delegatem Generała o. Tyberiuszem Menica dokonali przejęcia pobernardyńskiego klasztoru w Przasnyszu. Zadecydowało o tym wyborze przede wszystkim położenie klasztoru, z dala od centrum miasta, w odosobnieniu. Przełożonym klasztoru został Włoch o. Bartłomiej Rapetti. Do pierwszych zakonników pod koniec tego roku dołączali inni zakonnicy różnych narodowości. Kilkanaście lat obecności Pasjonistów w Polsce to okres dominacji Włochów, których zadaniem było zakorzenienie Zgromadzenia Męki Pańskiej na ziemiach polskich. Pod koniec roku 1930 było już w Polsce 11 braci oraz nowe powołania, a jedyny klasztor w Przasnyszu, gdzie był nowicjat i alumnat, stawał się za mały na tak szybko rozwijające się Zgromadzenie. W Przasnyszu mieściła się też tzw. Szkoła Apostolska, pełniąca rolę Niższego Seminarium, w której to szkole młodzi chłopcy zdobywając wykształcenie średnie mogli być również dobrym materiałem na przyszłych zakonników. Zaczęto rozglądać się za innym miejscem pobytu zakonników, a ułatwił te sprawę prymas kard. A. Hlond, który listownie obiecał pomoc. Będąc w 1932 r. w Poznaniu spotkał hr. B. Szembeka z Wysocka Wielkiego koło Ostrowa Wielkopolskiego, który zwierzył się mu z planów zaproszenia jakiegoś zakonu na swoje posiadłości i postawienia klasztoru we wsi Sadowie. We wspomnieniach spisanych przez hr. B. Szembeka , nadesłanych w 1980 r. przez jego córkę, pisze, iż pomysł ten miał być wyrazem wdzięczności z jego strony za ocalenie i przetrwanie wojny światowej. Powiadomieni o chęci zaproszenia zgromadzenia na Wielkopolskę, o. Bartłomiej Rapetti i o. Pius Falco udali się na rozmowę z fundatorem miejsca do Wysocka, a następnie do kardynała A. Hlonda w Poznaniu. Otrzymując 10 ha terenu, zagwarantowali sobie samotność i odosobnienie. Po uzyskaniu zgody prymasa na otwarcie klasztoru w Sadowiu na terenie diecezji poznańskiej oraz zgody o. Generała, rozpoczęto budowę piętrowego budynku i nieco mniejszej kaplicy. Po ukończeniu podstawowych prac dnia 6. listopada 1932 roku przy udziale prymasa Hlonda, dokonano poświęcenia obiektów. Ojciec Bartłomiej Rapetti witając zebranych gości wyraził nadzieję, "że kardynał będzie mógł znowu przybyć, by pobłogosławić w Sadowiu nowy kościół i klasztor. Wtedy ta kaplica będzie służyła dla rekolektantów, gdyż zamiarem Zgromadzenia jest, ażeby klasztor sadowski służył zakonowi jako dom rekolekcyjny, a zarazem jako dom nowicjatu Zgromadzenia". Patronem klasztoru został św. Józef, patron życia wewnętrznego. Powstał więc w Polsce drugi klasztor Pasjonistów, który przed wojną służył za miejsce nowicjatu aż do roku 1969. W latach 1934 - 1935 dobudowano budynki gospodarcze, stodołę, zalesiono pagórek i posadzono sad oraz postawiono trzy krzyże, na które materiał ofiarował m.in. hr. B.Szembek. Prekursorami sadowskiego klasztoru byli ojcowie: Bartłomiej, Pius i Juliusz Dzidowski, oraz brat Franciszek Użarowski.
                </p>
                <p>
                    Trudne warunki na początku pobytu Pasjonistów w Polsce były przyczyną wysyłania kleryków na studia i formację do Włoch. Drugą i ostatnią fundacją przyjętą przez Pasjonistów przed II wojną światową był poaugustiański klasztor w Rawie Mazowieckiej. Instytut otrzymał stare zabudowania oraz kościół od kard. A. Kakowskiego, dekretem z dnia 23.10.1937 r. W następnym roku odbył się ingres oraz porządkowanie budynków, odnawianie zdewastowanego kościoła i klasztoru. Pasjoniści przybyli tam ostatecznie w 1939 roku, kiedy przełożonym był o Juliusz Dzidowski. Przed samą jednak wojną doszło do nieporozumienia pomiędzy instytutem a miejscową Kurią w Warszawie, która chciała narzucić wbrew Regułom zakonnym i decyzjom władz zakonnych w Rzymie, stanowisko prefekta w szkole podstawowej w Rawie. W wyniku tego sporu jeden zakonnik musiał przenieść się do Sadowia, a klasztor pozostał tylko rezydencją z kustoszem na czele. Fakt utrzymania się placówki był jednak zbawienny dla późniejszych wydarzeń z czasów wojny.
                </p>
                <p>
                    II wojna światowa pociągnęła za sobą tragiczne skutki również wśród Pasjonistów w Polsce. Zgromadzenie liczące w tym kraju na początku wojny 50 zakonników: 18 kapłanów, 14 braci, 18 kleryków, rozmieszczonych w 3 klasztorach, zostało poddane prawdziwej próbie, sprawdzającej autentyczność powołania.
                </p>
                <p>
                    Klasztor w Rawie Mazowieckiej pozostał jako jedyny własnością Zgromadzenia, gdyż zarówno teren Przasnysza jak i Sadowia przyłączono do Rzeszy Niemieckiej. W wyniku ucieczki przed frontem albo likwidacji klasztorów polscy Pasjoniści zmuszeni byli opuścić te placówki. Także groźba aresztowań pojawiła się w Rawie, dlatego część zakonników zmuszona była do ucieczki na wschód , w kierunku Pińska, część zaś ukryła się w prywatnych domach, a kleryków rozesłano do domów rodzinnych. Po jakimś czasie klasztor ten zamienił się w wojenny szpital. Zakonnicy , którzy powrócili w 1940 roku (B. Kryszkiewicz, o. Stefan Szafraniec, o. Tytus Kołakowski, o. Leon Morawski, trzej klerycy), rozpoczęli działalność duszpasterską, dołączając także do niej opiekę nad rannymi żołnierzami. Placówka w Rawie była pod stałą obserwacją ze strony władz okupacyjnych. Klasztor służył także za schronienie dla przymusowych przesiedleńców z Wielkopolski, z północnego Mazowsza, z Polesia i w końcu dla mieszkańców Warszawy po upadku Powstania Warszawskiego w 1944 roku. Wszelkie pomieszczenia, łącznie z zakonną kaplicą skrzętnie wykorzystano do ulokowania tych wszystkich osób, które szukały schronienia. Udzielano wszelkiej pomocy: duszpasterskiej, sanitarnej, lokalowej i żywieniowej z udziałem także osób świeckich. Klasztor w Przasnyszu, który znalazł się w obrębie Rzeszy Niemieckiej, na początku wojny był oszczędzony w tragicznych losach wojennych. Służył za szpital wojskowy, a zakonnicy mieszkali początkowo w pralni, stolarni i służbówce, ostatecznie musieli przenieść się do sióstr Kapucynek. Klasztor został następnie przeznaczony na spichlerz zbożowy, dalej dewastowany i opuszczony. Wyposażenie sakralne zostało usunięte i wykupione przez miejscowego proboszcza kościoła farnego.
                </p>
                <p>
                    Dnia 2.04.1941 roku aresztowano całą tamtejszą wspólnotę wraz z o. przełożonym o. Sylwiuszem Pajewskim i wywieziono razem z tamtejszymi siostrami Kapucynkami Klaryskami do obozu koncentracyjnego w Działdowie (Soldau). Tragiczne momenty aresztowania Pasjonistów i Sióstr Kapucynek są opisane w Kronice klasztornej tychże sióstr w Przasnyszu. Jak relacjonują siostry Kapucynki, w obozie przetrzymywano Pasjonistów w bardzo ciężkich warunkach, bito ich, kopano i maltretowano. Na początku sierpnia wszyscy księża i duchowni, którzy mogli poruszać się o własnych siłach musieli stawić się w komendzie obozu celem sporządzenia listy i uzupełnienia danych personalnych. Następnie rozpoczęto wywożenie kapłanów w nieznanym kierunku, zaś liczne powiadomienia rodzin o zgonie w tym okresie świadczą o masowych egzekucjach. W obozie albo w tzw. lasku białuckim zamordowano także Pasjonistów, zbrodnia dokonana została prawdopodobnie 8 sierpnia 1941 roku. Męczeńską śmiercią zginęło łącznie siedmiu zakonników: o. Czesław Załga o. Józef Rosiński o.Laurencjusz Bugaj o.Sylwiusz Pajewski, przełożony, br. Tadeusz Siwoski, br. Leonard Kuskowski i br. Antoni Glinka, którzy zmarli prawdopodobnie wcześniej w obozie. Ojciec Czesław Załoga zdążył napisać jeszcze list do brata Franciszka Użarowskiego o swym pobycie w obozie. Był przekonany o wypełnianiu się woli Bożej i przeświadczony o słodyczy tego strasznego cierpienia dla Chrystusa.
                </p>
                <p>
                    Również tragiczny los spotkał klasztor zbudowany przez Pasjonistów w Sadowiu, gdzie przebywało kilku zakonników do lipca 1941 roku, po powrocie z tułaczki. Wówczas to klasztor został przejęty przez Niemców i przeznaczony na szkolenie młodych nazistów , tzw. Hitlerjugend. Usunięto trzy stojące krzyże, wzniesiono maszt ze swastyką. Kościół sadowski został zamieniony na salę taneczną i gimnastyczną , w miejsce ołtarza ustawiono kominek ze swastyką , rozebrano również dzwonnicę. Zakonnicy zdążyli przy pomocy miejscowej ludności ukryć sprzęt kościelny oraz dzwony. Część zakonników udała się do domów rodzinnych, zaś niektórzy ukrywali się w pobliskich okolicach udzielając się duszpastersko, w Wysocku, Sadowiu i Ostrowie Wielkopolskim. Jeden z ukrywających się zakonników, br. Wacław Kamieński , aresztowany przez gestapo u miejscowego gospodarza, zmarł w obozie w Dachau prawdopodobnie w 1943 roku.
                </p>
                <p>
                    W ciągu kilkunastu lat przed wojną na terenach polskich przebywało wielu Pasjonistów z innych krajów, wielu z nich było pochodzenia polskiego. II wojna światowa przerwała tę misje i zakonnicy przebywający w wiceprowincji polskiej zdani byli na siebie. Niektórzy w czasie wojny przebywali w Rzymie i ten czas przeznaczyli na studia specjalistyczne, co przydało się przy odtwarzaniu i wzmacnianiu Zgromadzenia po powrocie do Polski. W roku 1946 nowicjat w Polsce liczył zaledwie kilku nowicjuszy, potem stopniowo liczba się powiększała osiągając w roku 1950 trzynastu, co wzmocniło liczebnie polską wspólnotę, Pasjonistów. Przez lata powojenne w Przasnyszu mieścił się również dom studiów, którego rektorem był biblista, o. Michał Stolarczyk.
                </p>
                <p>
                    W pierwszych latach pobytu Pasjonistów w Polsce utworzono jednostkę administracyjną zwaną komisariatem, na którego czele stanął o. Bartłomiej Rapetti, który pełnił funkcję komisarza generalnego aż do momentu opuszczenia Polski w 1936 r. Wtedy jego następcą został o. Stanisław Michalczyk. Następnie utworzona została wiceprowincja zaś wiceprowincjałem został o. Juliusz Dzidowski. o. Stanisław Michalczyk w czasie wojny był przełożonym w Rawie i właściwie także pełnił obowiązki wiceprowincjała, gdyż o. Juliusz Dzidowski przebywał w tym czasie poza klasztorem. Po drugiej wojnie światowej w 1958 r. Stolica Apostolska erygowała polską prowincję zakonna Pasjonistów pod wezwaniem Wniebowzięcia NMP (ASSUM). Pierwszym prowincjałem został mianowany w Rzymie podczas Kapituły Generalnej ówczesny wiceprowincjał o. Jan Wszędyrówny, wychowanek o. Bernarda Kryszkiewicza z lat wojennych, obecnego Sługi Bożego, którego proces beatyfikacyjny jest w toku. W latach powojennych objęto też inne placówki, w Warszawie, Wiśle, Łodzi, Olsztynie oraz na Ukrainie w Smotryczu oraz w Czechach na Morawach, Warszawie-Białołęce. Polacy też pracowali w krajach takich jak Zair, Anglia, Szwecja, RPA. W ostatnich latach liczba Polaków z Zgromadzeniu Męki Pańskiej zmniejszyła się i wynosi ok. 60 członków.
                </p>

            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "InPoland"
    }
</script>

