<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    Św. Gabriel Possenti<br />
                    (1838 - 1862)
                </h1>
                <img src="../assets/images/saints/gabriel-possenti.jpg" alt="">
                <p>Miał na imię Franciszek, a to dlatego, że pochodził z Asyżu , miejsca znanego od innego Franciszka.  Pochodził ze znanej i szanowanej rodziny, którą można określić dzisiaj jako elitę społeczną Państwa Kościelnego. Urodził się 1 marca 1838 r. w jednym z najokazalszych gmachów Asyżu, ponieważ ojciec był wtedy gubernatorem tej prowincji. Wszyscy byli z Franciszka dumni, i rodzice i nauczyciel, i księża Jezuici, którzy pragnęli go widzieć w swoim zakonie. Jemu jednak nie w głowie były ciągłe modlitwy i pokuta, ale tańce, humor, wesołość, popularność wśród kolegów. Stawał się bywalcem wielu uroczystości zapraszany na różne przyjęcia. Zawsze jednak modlił się w domu na różańcu, tak jak nauczyli go rodzice. Momenty głębszego zastanowienia nad swoim życiem przychodziły w trudnych chwilach, kiedy zmarła jego matka, a potem jego siostra w czasie epidemii. Podczas procesji obrazu Maryi doznał ogromnego wzruszenia i wtedy postanowił w sercu wstąpić do jakiegoś klasztoru. Nie wszyscy byli z tego radzi. Ojciec szykował dla niego dobrą partię i kiedy dowiedział się o zamiarach syna, wtedy zaprotestował. Modlitwa Franciszka skruszyła jednak serce ojca, który w końcu obiecał mu swe błogosławieństwo. Jeszcze na dzień przed odjazdem do klasztoru Pasjonistów wziął udział w ostatnich swych popisach, odbierając w kolegium w Spoleto dyplom najlepszego ucznia oraz biorąc udział w przedstawieniu. Któż mógł przypuszczać , że ten wytworny i elegancki młodzieniec, wystrojony tak, że oczy dziewcząt za nim krążyły, na drugi dzień miał to wszystko odrzucić i zamknąć się w surowym zakonie? Jednak tak się stało! Gdy skończył szkołę średnią wstąpił do Pasjonistów.</p>

                <p>
                    W listach do rodziny pisze o spokoju, jaki odnalazł w klasztorze. Podejmował heroiczne akty wyrzeczenia i pokuty, stawiał sobie bardzo wysokie wymagania, bo chciał, żeby jego życie zakonne było doskonałe.
                </p>
                <p>
                    Chciał czynić tylko dobro, całego siebie oddać Bogu. Najgoręcej chciał kochać Matkę Bożą Bolesną, którą wziął sobie za patronkę. Przygotowywał się do święceń kapłańskich. Jednak zapadł na zdrowiu.
                </p>
                <p>
                    Choroba okazała się poważna. Była to nieuleczalna wówczas gruźlica. Zmarł spokojnie 27 lutego 1862 r. mając zaledwie 24 lata.
                </p>
                <p>
                    W górskim klasztorze w Isola del Gran Sasso wyrosło ogromne sanktuarium odwiedzane corocznie przez setki tysięcy pielgrzymów, głównie młodzieży włoskiej.
                </p>
                    <p>
                        Gabriel zaledwie 6 lat życia spędził w zakonie, gdyż umarł na gruźlicę, ale ten czas wystarczył do tego, aby uznano go za świętą osobę. Stał się zakonnikiem wiernym w wypełnianiu Reguły i innych przepisów. Ukochał Maryję Bolesną, przyjął imię Gabriel i marzył o tym , by być misjonarzem, kaznodzieją , apostołem Maryi. Jego wrodzona wesołość i humor nie zniknęły, nawet wtedy gdy już chory leżał w łóżku. Zawsze pełen gorliwości i miłości do Jezusa Ukrzyżowanego i Jego Matki Bolesnej. Jest patronem młodzieży włoskiej i zakonnej. Co roku w czasie wakacji do jego sanktuarium w Teramo (San Gabriele) zjeżdżają się bardzo liczne grupy młodych ludzi.
                    </p>
                <p>
                    Jego kult potwierdził Pius X 14 maja 1905, a następnie beatyfikował go 31 maja 1908. Uroczystości odbyły się najpierw w Watykanie, w obecności kardynałów, później w Rzymie wśród zaproszonych pasjonistów i przedstawicieli wszystkich prowincji zgromadzenia. Wśród gości znaleźli się m.in. kierownik duchowy i spowiednik św. Gabriela, o. Norbert, CP oraz cudownie uzdrowiony za wstawiennictwem świętego, Signor Dominico Tiberi.
                </p>
                <p>
                    Wolą Leona XIII i Piusa X było, aby pasjoniści wspominali bł. Gabriela 31 maja.
                </p>
                <p>
                    Kanonizacji bł. Gabriela dokonał Benedykt XV 13 maja 1920 roku.
                    Ks. Jan Twardowski napisał o nim wiersz, w którym  czytamy m.in <i>"(...) O Gabrielu bledziutki, z bolesnym w ręku obrazkiem;
                    jesteś mi cały - gdy kocham.  Szczęśliwym wynalazkiem."
                </i>

                </p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Gabriel"
    }
</script>

<style scoped lang="scss">
    @import "../assets/scss/style";

</style>


