jawia się ona w stylu życia wspólnotowego oraz w działalności apostolskiej. W klasztorach pasjonistów nie ma sztucznych podziałów czy klas. Pod krzyżem sprawy prestiżowe maleją. Rozróżnienia wynikające w sposób naturalny z funkcji, rekompensuje atmosfera rodzinna, duch braterski, który panuje przy wspólnym stole, we współpracy, w podejmowaniu wspólnych decyzji, w dzieleniu się wzajemnie trudnościami, a także radością.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Pomimo ewolucji, jaką przechodziło zgromadzenie przez dwieście osiemdziesiąt lat swej historii, nie zatraciło ono swoich ideałów wytyczonych przez Założyciela. W Konstytucjach czytamy: \"Świadomi, że Męka Chrystusa trwa na tym świecie aż do powtórnego przyjścia Chrystusa w chwale, dzielimy trudy i radości ludzkości w jej drodze do Ojca. Pragniemy dzielić nieszczęścia wszystkich, a zwłaszcza ubogich i opuszczonych, podnosząc ich na duchu, pomagając nieść ciężar ich utrapień. Przez moc Krzyża, będącą mądrością Bożą, dążymy usilnie do poznania i usunięcia przyczyn zła, które dręczy ludzi. Dlatego też nasze posłannictwo opiera się na ewangelizacji poprzez posługę Słowa Krzyża tak, aby wszyscy mogli poznać Chrystusa i moc Jego Zmartwychwstania. Każdy człowiek, uczestnicząc w cierpieniach Chrystusa, może stać się podobnym do Niego w śmierci, aby mieć udział w jego chwale. Wszyscy uczestniczymy w tym apostolstwie. Każdy według swoich zdolności, talentów i możliwości\" (Konstytucje Zgromadzenia Pasjonistów, 3).\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Uczestnictwo pasjonisty w Męce Chrystusa otrzymuje w profesji zakonnej wymiar prawny, określony mianem \"pasyjnej konsekracji\". \"Nasz udział osobisty, wspólnotowy i apostolski w Męce Chrystusa wyrażamy specjalnym ślubem. Przez ten ślub zobowiązujemy się szerzyć pamiątkę Męki Chrystusa słowem i czynem, a także pogłębiać świadomość o jej znaczeniu i dla każdego człowieka i dla życia świata\" (Konstytucje Zgromadzenia Pasjonistów, 6). Ta szczególna dedykacja, wyrażająca się w pasyjnej interpretacji trzech ślubów zakonnych: ubóstwa, czystości i posłuszeństwa. Wszyscy zakonnicy usiłują realizować je łącznie, w trudzie dnia codziennego.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Takie ustawienie życia rodzi, w miarę jak jest realizowane - radość. \"Chociaż bowiem, w centrum naszej uwagi stoi Krzyż i Jezus na nim, nie jesteśmy smutni, gdyż z tej śmierci rodzi się życie, w tej żałobie kryje się wielka nadzieja, a Krzyż jest znakiem miłości i zwycięstwa\". Dzięki Męce Chrystusa, zakończonej zmartwychwstaniem, obce jest chrześcijaństwu cierpiętnictwo i dlatego pasjoniści, usiłują przezwyciężyć w sobie i wokół siebie wszelki pesymizm, zastępując go pogodnym optymizmem. Taki jest właśnie ideał pasjonisty, realizowany od początku, aż po dzień dzisiejszy.\n            ")])])])])}]
