var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"block"},[_c('div',{staticClass:"page"},[_c('div',{staticClass:"page__blur"}),_vm._v(" "),_c('div',{staticClass:"page__content"},[_c('h1',[_vm._v("\n                Św. Karol Houben"),_c('br'),_vm._v("(1821 - 1893)\n            ")]),_vm._v(" "),_c('img',{attrs:{"src":require("../assets/images/saints/karol-houben.jpg"),"alt":""}}),_vm._v(" "),_c('p',[_vm._v("\n                Pochodził z miasteczka prowincjalnego Munstergelen w Holandii. W małej wsi, znajdującej się w  graniczącej z Niemcami Limburgii. Był czwartym z dziesięciu dzieci młynarza. Miał pobożnych i ciężko pracujących rodziców -  to od nich odebrał Karol podstawowe, chrześcijańskie wychowanie. Codziennie odmawiał wraz z nimi poranne modlitwy , otrzymywał od nich  błogosławieństwo do pracy, i wraz z nimi wieczorem, na zakończenie dnia odmawiał różaniec. Był łagodnym, prostym i pobożnym  młodzieńcem.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Poza  wyjątkową pobożnością, jedną z cech charakteru Andrzeja była jego wyjątkowa wytrwałość. Przez 10 lat chodził z trudem dwumilową drogą  do szkoły średniej w Sittard, tak , że wieśniacy nie mogli powstrzymać się od komentarzy : „czy ten głupi syn młynarza nigdy nie skończy swej nauki?” Ale Andrzeja nie  pokonały te trudności. Wyznaczył sobie cel do osiągnięcia i z ponurą determinacją charakteryzującą jego drogę, hartował się i pokonywał każdą przeszkodę. Celem  było kapłaństwo. Od tak dawna jak tylko pamiętał, chciał zostać księdzem. Nie pamiętał jednak, kiedy podjął taką decyzję. Ale kiedy już to uczynił, był nieugięty w swym zamierzeniu. Nie mogły powstrzymać go nawet braki w inteligencji. Z pomocą Bożą potrafił walczyć i pokonywać każą niedoskonałość.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Często zdarzało się, że gdy jego matka  odkładała po pracowitym dniu na bok szycie lub cerowanie, i przed snem rozpalała ogień na noc , Andrzej ciągle jeszcze męczył się nad  swoimi ćwiczeniami. Stawała wtedy nad nim i łagodnie kładąc ręce na jego ramionach szeptała: „Andrzeju, synku, czy wszystkie te studia  nie męczą cię? Czy nie lepiej byłoby odłożyć na bok  książki i pomóc ojcu? Dużo czasu upłynie zanim ukończysz twe studia”.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Po szczególnie ciężkim dniu w szkole, przychodziła pokusa, aby myśleć tak, jak matka. Po długich i daremnych próbach rozwiązania swych wielu problemów bywał zniechęcony; Coroczne  wydatki, które rodzice z trudem pokrywali, były powodem myśli o rezygnacji ze swoich wydłużonych studiów. Myśl, że mógłby być bardziej potrzebny swoim rodzicom, przy pracy w ojcowskim młynie lub na polu, często wydawała mu się nie tylko sensowna i praktyczna, ale także wysoce odpowiednia.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Jego postanowienie ciągle się chwiało ,ale podczas tych ciężkich lat,  zaowocowało to  w końcu –  w jego przekonaniu – tym , że Bóg powołujący go do jakiejś wielkiej pracy, oddaje mu z powrotem zaufanie i silną wolę.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                W końcu jego matka była tak o niego zaniepokojona , że zdecydowała się skonsultować z księdzem parafialnym, ojcem Delaheye. Ksiądz odrzekł matce: „Pozwól mu kontynuować naukę. Ten chłopiec będzie wielkim człowiekiem.”\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Andrzej często spędzał  w drodze z domu do szkoły długie chwile na modlitwie przed Najświętszym Sakramentem. Nieraz więc już zmierzchało, gdy wracał do domu. Jego koledzy mogli potwierdzić, że znał tylko dwie drogi w miasteczku: do szkoły i do kościoła. Czasami, gdy zbliżała się pora rodzinnego różańca, a on się jeszcze nie pojawiał,  matka coraz bardziej zaniepokojona  wysyłała  na poszukiwanie Andrzeja drugiego syna. Ten od razu kierował się do kościoła i zaraz go odnajdywał, modlącego się przed Najświętszym Sakramentem.\n                Na wiosnę 1840 r. otrzymał wezwanie do służby wojskowej. Miał 19 lat. Czekano na losowanie młodych ludzi, którzy mieli trafić do wojska. 2 marca wylosował nieszczęśliwy numer. Został włączony do I – go  Regimentu Pieszego i wpisany na lipiec.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Trafił w miejsce, gdzie musiał znieść wiele upokorzeń. Także jako katolik. Koledzy odnosili się do niego ordynarnie i grubiańsko. Ale wszyscy zauważyli , że dużo poświęca czasu w miejscowym kościele, niektórzy nawet patrzyli na niego z podziwem i respektem. Przyjmował Najświętszy Sakrament w każdą niedzielę rano.\n                W wojsku odnalazł swe prawdziwe powołanie. Jeden z kolegów – niejaki Raaymakers, miał brata , który wstąpił właśnie do Pasjonistów w Ere, w Belgii. Andrzej nigdy nie słyszał o tym zakonie, ponieważ wszystkie  zostały skasowane w Holandii i nietolerowanie aż do 1842 r.  Raaymakers był pod wrażeniem, pokazywał Andrzejowi listy od swojego brata. Akurat był to żarliwy okres nowicjatu dla jego brata, okres, w którym doznawał życiowego szczęścia w zakonie. Andrzej był bardzo zainteresowany. To było dokładnie to sposób na życie, za którym tak tęsknił i dla którego tak ciężko pracował.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Dnia 9. Października wrócił do domu, ponieważ jego ojciec zapłacił po zbiorze żniw kaucję za służbę. Andrzej powrócił do książek i nauki. Miał opóźnienia. Jeden z profesorów otworzył prywatną szkołę w Broek Sittard i tam Andrzej nadrabiał studia. Stary nauczyciel  był zdumiony wybitną poprawą ucznia, który z łatwością studiował i nadganiał program. Profesor Shyre relacjonował: „wyglądało to tak, jakby ręka Boga go dotknęła” . Okazało się , że ten profesor znał Pasjonistów w Ere i przez to przybliżył mu życie klasztorne, bardziej niż kolega z wojska. Jeszcze bardziej usłyszał, przekonał się do tego, by zostać , idąc za głosem Boga, członkiem Zgromadzenia Pasjonistów. Rozmawiał o tym ze swym ojcem. Matka zmarła mu krótko przedtem. Jego ojciec radził się ojca Delahaye, który natychmiast poradził mu napisać do przełożonego klasztoru św. Michała w Ere. Andrzej został przyjęty do nowicjatu dnia 5. Listopada 1845 r. Gdy zbliżał się dzień odjazdu pojawiły się  znaki wewnętrznej walki . Rozmawiał z ojcem, który odradzał mu tej decyzji , jednak Andrzej był zdecydowany. Swej siostrze Sybilli odpowiedział: „Dla sprawy Naszego Pana nic nie zrobię, poza tym, że chętnie spędzę me życie na pustyni. Noc przed odjazdem po spakowaniu bagaży, po raz ostatni ukląkł wraz z rodziną do różańca. Potem przeszedł z pokoju do pokoju , zatrzymał się przed obrazem matki, i wtedy siostra Krystyna powiedziała: „ wiem co myślisz. Myślisz, że nigdy nie zobaczysz tych rzeczy więcej.” „Tak, droga”, odpowiedział.  Następnego ranka były pożegnania. Z ojcem, przy grobie matki. Ojciec pobłogosławił go, a następnie Andrzej opuścił cmentarz. W ponury dzień listopada 1845 r. w klasztorze Pasjonistów  blisko Tournai w Belgii, Andrzej Houben pojawił się po raz pierwszy. Była to pierwsza fundacja o poza Italią. Barberi czynił przygotowania do działalności apostolskiej w Anglii. Przyjęto Andrzeja z uprzejmością i badawczym nastawieniem. Ujrzano młodego człowieka, wysokiego, zbudowanego, smagłą postać i silne rysy twarzy. Miał dobrą opinię od proboszcza, o. Delahaye, który zapewniał o jego solidnych cnotach i miłości do modlitwy.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                W nowicjacie Andrzej czyścił podłogi, zamiatał schody, obierał ziemniaki, mył talerze po obiedzie i wykonywał wiele innych obowiązków. Był też surowo karany za drobne nawet całkiem zmyślone błędy. Ani żołnierz Raaymakers ani profesor Schyren nie ostrzegli go przed tym co miało go spotkać. Był jednogłośnie pochwalony przez ojców na kapitule. Wdział habit 1 dnia  grudnia. U Pasjonistów istniał zwyczaj zmiany imienia przyjętego na chrzcie, na inne, i miał to być wyraz wejścia w inny, wyższy stan życia. W nowicjacie nadano mu imię Karol od św. Andrzeja.  W nowicjacie brat Karol był „ zatracony w Bogu”. O jego duchowej stronie życia nie wiele wiemy. Nie zostawił żadnych napisanych słów dotyczących życia duchowego. Był małomówny, zamknięty pod tym względem. Był jedną z tych dusz, które przez codzienny przykład nieświadomie oddziaływały na innych. Był dokładny w zachowaniu Reguły, prosty i przyjacielski w stosunkach z innymi, łagodny i szczery, naturalnie miłosierny, dobrego ducha i naturalnej wesołości podczas rekreacji , wszyscy go lubili. Rankiem dnia 10 dnia grudnia 1846 roku złożył profesję . Następnie rozpoczął kurs filozofii i teologii. Życie studenta było fizycznie i intelektualnie ciężkie, monotonne w swych obowiązkach: dzień po dniu , rok po roku. Spędzał godziny przed Najświętszym Sakramentem: od 2 do 3 rano, potem od 6 do 9 rano.  Różne obowiązki były poprzedzielane nauką i modlitwą. Musiał domyślać się, że są plany wysłania go do Anglii i był przygotowywany  nie tylko do szerzenia wiary katolickiej , ale i do znoszenia różnych ataków. O. Dominik , doskonały teolog przygotowywał w tym kierunku młodych pasjonistów.\n                Dnia 21 grudnia 1850 r. o. Karol został wyświecony przez biskupa Labisa, biskupa Tournai.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                „Błagam Cię, przyślij mi człowieka, dobrego zakonnika, zamiast uczonego Salomona” , pisał o. Dominik do swego przełożonego w Rzymie. Takim był Karol Houben. Sześć miesięcy po święceniach Karol przybył do Staffordshire w Anglii.\n                Od tego czasu Karol Houben zaczął gorliwie pracować dla zbawienia ludzi w Irlandii i Anglii. Spotkało go wiele przykrości i przeszkód. Za jego przyczyną dokonywały się także uzdrowienia, co wywołało ze strony władz angielskich pewne zaniepokojenie. Ale przede wszystkim zasłynął jako kierownik duchowy, spowiednik i przewodnik biedoty irlandzkiej w Anglii i samej Irlandii. Był duszpasterzem ludzi najuboższych, zwłaszcza robotników irlandzkich w Anglii. Chorował długo , ale nie stronił od umartwień. Także przeżywał stany mistyczne. Zmarł 5  stycznia 1893. Jan Paweł II beatyfikował go w roku 1988. Kanonizował go papież Benedykt XVI w 2007 r. w Rzymie.\n            ")])])])])}]
