ko odnowieniem\n                ślubu wypełnienia charyzmatu i szerzenia pamięci i kultu Męki Pańskiej. Zdecydowany był też na\n                przyjmowanie innych towarzyszy do swej wspólnoty. Pierwszą samotnią przyjętą od bp. Gattinara było\n                Monte Argentario nad Morzem Adriatyckim. W roku 1725 poznał osoby, które stały się jego protektorami\n                w Rzymie. Papież Benedykt XIII zachęcił go do tworzenia grupy ustnie aprobując nowy instytut. Św.\n                Paweł od Krzyża pracował w szpitalu w Rzymie i tam również przyjął święcenia kapłańskie. Dnia 15\n                maja 1741 r. została zatwierdzona pierwsza Reguła nowego instytutu, pod nazwą Zgromadzenie Ubogich\n                Kleryków Bosych pod wezwaniem Najświętszego Krzyża i Męki Jezusa Chrystusa. W lipcu tego roku\n                pierwszych sześciu towarzyszy złożyło trzy śluby zakonne dodając czwarty ślub szerzenia nabożeństwa\n                i kultu Męki Chrystusa przez medytację i nauczanie. W 1769 r. papież Klemens XI zatwierdził\n                zgromadzenie zakonne ze ślubami prostymi i przywilejami przysługującymi zakonom żebrzącym. Paweł\n                Daneo po otrzymaniu duchowego oświecenia na początku swej drogi przez prawie 40 lat pozostawał w\n                nocy duchowej, która została mu odjęta pod koniec życia.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                W chwili śmierci Pawła od Krzyża instytut liczył sobie 176 zakonników i 12 klasztorów. Do połowy XIX\n                wieku instytut działał we Włoszech. Potem klasztory zostały założone w Belgii, Anglii, USA,\n                Hiszpanii , następnie w Ameryce Południowej, Australii i Francji. Pierwsze zagraniczne fundacje\n                istniały jednak w Walii i Bułgarii od 1782 roku pod bezpośrednim zwierzchnictwem Kongregacji d/s\n                Krzewienia Wiary. W wieku XX Zgromadzenie zostało przeszczepione m.in. do Irlandii, Palestyny,\n                Polski i Niemiec.\n            ")]),_vm._v(" "),_c('p',{staticClass:"center"},[_c('strong',[_vm._v("\n                    STRONA OFICJALNA ZGROMADZENIA PASJONISTÓW:\n                ")])]),_vm._v(" "),_c('p',{staticClass:"center"},[_c('span',{attrs:{"onclick":"navigator.app.loadUrl('http://www.passiochristi.org/', {openExternal : true});"}},[_vm._v("www.passiochristi.org")])])])])])}]
