<template>
    <div class="block">
        <div class="page">
            <div class="page__blur"></div>
            <div class="page__content">
                <h1>
                    Bł. Ferdynand Grimoaldo Santamaria<br />
                    (1883 - 1902)
                </h1>
                <img src="../assets/images/saints/grimoaldo-santamaria.jpg" alt="">
                <p>Jego rodzina była prosta, pobożna , mieszkająca we Włoszech. Matka i ojciec często modlili się na różańcu,  uczyli modlitwy swoje dzieci, rodzice byłi bardzo uczynni, pełni dobroci wobec innych ludzi. Ferdynand przyjął pierwszą Komunię Świętą w 1891 r. przygotowując się do niej bardzo starannie.  Wcześniej został do niej dopuszczony, pomimo że dzieci w jego wieku musiały jeszcze długo czekać na to wydarzenie. Odtąd Ferdynand często przyjmował Pana Jezusa, często przychodził na Mszę św., zatapiał się w modlitwie. Został ministrantem i tak często służył do rannej Mszy św., oraz na popołudniowych nabożeństwach, że proboszcz pogratulował rodzicom takiego  chłopca.  Wstąpił do Konfraterni Niepokalanej, zrzeszającej dzieci, a potem do chóru parafialnego, bowiem był dobrym śpiewakiem.  Pasjoniści prowadzili w okolicach rekolekcje parafialne, uczyli pieśni, których młody Ferdynand szybko się nauczył. Sam po wyjeździe misjonarzy nauczał katechizmu i uczył pieśni, naśladując w ten sposób zakonników. </p>
                <p>
                    Któregoś dnia ojciec jego zaproponował mu kupno konia i pojazdu, aby mógł zostać woźnicą i zaczął uczyć się zawodu. Obiecał mu szybki zarobek i to że nie będzie się martwił o przyszłość. Młodzieniec jednak odparł, ze jego karierą jest zostać zakonnikiem. Po tej rozmowie ojciec nie ustępował, chciał koniecznie , aby jego syn  pracował i zarabiał na życie. Nastawał wciąż na niego  aż w końcu ten odpowiedział ojcu: "Widzisz tato jak woda ta płynie i się nie cofa? Tak płyną dni naszego życia. Płyną do przodu i nie wracają. Po cóż przywiązywać się do świata? " W końcu ojciec ustąpił. Niedaleko osiedlili się Pasjoniści, w miejscowości Pontecorvo. Ferdynand w tym czasie wstąpił do szkoły prowadzonej przez księdza Antoniego Roscia, który był związany z zakonem. Chłopak starał się dobrze uczyć tak, aby szybko ukończyć naukę i wstąpić do Pasjonistów. Sprawę przyjęcia do zakonu załatwiali mu sami zakonnicy, ponieważ znali chłopaka i jego sytuację. Pożegnał się z rodzicami, jego ojciec odwiózł go nawet powozem na stację kolejową, na pociąg do Rzymu. W końcu wysiedli na małej stacyjce w Anagni i udali się do klasztoru Matki Bożej  w Pugliano . Tam zastała ich cisza, skupienie i modlitwa. Ferdynand uczuł wyjątkową atmosferę tego klasztoru.  Tym bardziej iż rozpoczął się wielki Tydzień, czas pokuty i rozpamiętywania   Męki Chrystusa. Po krótkim czasie Ferdynand przeżył rekolekcje a następnie obłóczony w czarny pokutny habit rozpoczął nowicjat. Przyjął wówczas oficjalnie imię Grimoalda od Oczyszczenia NMP.
                </p>
                <p>
                    Ofiarował się Przeczystej Dziewicy mając 18 lat. W nowicjacie przełożeni mieli z nim kłopot - nie mogli go przyłapać na żadnym potknięciu,  uchybieniu, błędzie w wypełnianiu obowiązków i w przestrzeganiu Reguły.  Nowicjusz Grimoaldo starał się pilnie naśladować młodego kleryka, Gabriela, który wówczas uznany powszechnie był za świętego. Złożył  swoje 4 śluby zakonne, przyjął emblemat, godło Męki Pańskiej, i otrzymał Regułę. Życie późniejsze wypełnione było ścisłym przestrzeganiem Reguły zakonnej, conocnym wstawaniem na modlitwy,  postem, biczowaniem, rezygnacją ze świeckich rozrywek. Takie wówczas obowiązywały zasady życia zakonnego. Wkrótce rozpoczął studia filozoficzne w klasztorze w Badia di Ceccano.  Studiował i modlił się za tych, którzy wówczas pracowali apostolsko głosząc rekolekcje. Studia były monotonne, miał trochę zaległości w nauce, dlatego musiał wiele pracować i przyjmował też chętnie pomoc w nauce od innych współbraci. W zamian za to pomagał albo i zastępował ich w pracach fizycznych, gdyż był dobrze zbudowany i silny. W październiku 1902 po Wszystkich Świętych klerycy udali się na wycieczkę, podczas której Grimoaldo poczuł się źle. W klasztorze lekarz stwierdził zapalenie opon mózgowych. Młody kleryk utracił przytomność.  Zaczął więc chorować, leżeć w łóżku. Chorego odwiedzali współbracia i budowali się jego cierpliwością, pokorą zdaniem się na wolę Bożą. Mimo wielu starań, jego życie zbliżało się ku końcowi. Jeszcze musiał w czasie swej choroby przeżyć walkę z siłami szatana, ponieważ znaleziono go krańcowo wykończonego przy zdemolowanym i przewróconym łóżku.  W środę 19 listopada wyspowiadał się, przyjął Komunię Świętą,  i powiedział , iż odejdzie  przed zachodem słońca. Czuł się jednak szczęśliwy, spokojny. Rzeczywiście umarł wieczorem, z rumieńcem na twarzy. Wieść o jego świętości rozeszła się wśród mieszkańców Italii a także wśród emigrantów w USA.  Tam też wielu jego rodaków otrzymywało łaski za jego wstawiennictwem.  Jan Paweł II dokonał beatyfikacji kleryka Grimoaldo Santamaria  dnia  29 stycznia 1995 roku.
                </p>
