oleceń przełożonych oraz choroby widać było w jego licznych listach do rodziny. Wszystko przyjmował jako wolę Bożą. Przed swą śmiercią zachował pogodę duch, czym budował swych współbraci. Umarł cicho z modlitwą na ustach 1916 r. potem jego szczątki przeniesiono do Courtrai. Jan Paweł II dokonał jego beatyfikacji dnia 30 września 1982 roku.\n            ")])])])])}]
