czędzał grosze aby zamówić intencję za swego zmarłego ojca.")]),_vm._v(" "),_c('p',[_vm._v("\n                W 1880 r. poznał Pasjonistów , którzy przybyli na stałe sanktuarium do Casale, niedaleko rodzinnej wsi Alojzego. Misjonarze głosili misje ludowe, z których korzystał także młody Alojzy. Mały Campidelli był zachwycony wyglądem i postawą Pasjonistów! Marzeniem stało się nosić taki strój i być zakonnikiem.  Zapisał się do świeckiego stowarzyszenia Św. Pawła od krzyża, otrzymał Szkaplerz Męki Pańskiej  z emblematem Pasjonistów.  Miał 12 lat , kiedy w wyniku tak wielkiego pragnienia, udał się z matką do klasztoru w Casale z prośbą o przyjęcie do zakonu. Przeor klasztoru rozpoznał w nim  wyjątkowego kandydata, lecz odesłał do domu, gdyż  musiał ukończyć 14 lat, zanim mógł być przyjęty do alumnatu.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                W końcu w roku 1882 , po mszy św. został odwieziony przez właściciela majątku i księdza do klasztoru w Casale, gdzie rozpoczął Szkołę Apostolską dla chłopców i kandydatów na kapłanów. Przyobleczony został w habit zakonny przybierając nowe imię Pio od św. Alojzego. Od samego początku wielkodusznie poświęcił się Bogu, często uciekał się do Ran Chrystusa,  często przyjmował Eucharystię i przebywał przy tabernakulum, modlił się do Matką Bożą. Wczytywał się w Pismo Św., korzystał  z rad spowiedników, jednym słowem konsekwentnie dążył do świętości.  Ze spisanych przez niego notatek z rachunku sumienia można wywnioskować o jego dużej determinacji i pragnieniu bycia świętym.   W 1887 r. przyjął niższe święcenia  i kontynuował kurs teologii. Liczył wówczas 19 lat. Szykował się do subdiakonatu i diakonatu, ale Bóg zdecydował inaczej. Zachorował na gruźlicę płuc, na którą to wówczas nie było lekarstwa. Był to więc wyraźny znak od Boga iż musi poświęcić swe życie. I tak też się stało. Umarł 3 listopada 1889 r mając 21 lat i 6 miesięcy, na końcu wypowiadając słowa kierowane do Boga i Maryi.  Po jego śmierci    do klasztoru zaczęły napływać wiadomości o łaskach otrzymanych za jego przyczyną. Odbywały się także całe pielgrzymki do sanktuarium w Casale. Kiedy pewnego dnia pielgrzymka z Rimini przybyła do znanego o. Pio, kapucyna, ten  zauważył : \"dlaczego przybywacie do ojca Pio kapucyna, przecież macie u siebie świętego Pio pasjonistę\".\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Jan Paweł II po odpowiednim procesie kanonicznym wyniósł go na ołtarze 17 listopada 1985 roku.\n            ")])])])])}]
