var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"block"},[_c('div',{staticClass:"page"},[_c('div',{staticClass:"page__blur"}),_vm._v(" "),_c('div',{staticClass:"page__content"},[_c('h1',[_vm._v("\n                Bł. Dominik Barberi"),_c('br'),_vm._v("(1792 - 1849)\n            ")]),_vm._v(" "),_c('img',{attrs:{"src":require("../assets/images/saints/dominik-barberi.jpg"),"alt":""}}),_vm._v(" "),_c('p',[_vm._v("Dominik urodzony był w Viterbo w ówczesnym Państwie Papieskim, w rodzinie chłopskiej ale nie ubogiej. Jego ojciec, Giuseppe zmarł wcześnie, zostawiając żonę z licznym potomstwem, najmłodszy Dominik miał wówczas 4 lata. Matka, Maria Antonina, kobieta pełna prostoty  i głębokiej wiary nauczyła swych dzieci także radości życia i zawierzenia Bogu. Zmarła gdy Dominik miał 11 lat. Musiał rozstać się z domem rodzinnym, dzieci zostały rozdzielone pomiędzy krewnych. Brat rodzinny Dominika, Salvatore, zapragnął wieść życie pustelnicze, założył pustelnię w pobliżu dawnego domu i usiłował nakłonić Dominika do przyłączenia się. Skończyło się jednak na tym że porzucił swą pustelnię i wstąpił do pasjonistów. Dominik trafił pod opiekę ciotki, która liczyła na jego pomoc w dużym gospodarstwie. Małżeństwo Pacelli pokochali swego nowego syna i mieli dużą pociechę z jego pomocy. Jednak zabrakło w tym czasie edukacji. Samo pisanie i czytanie nie wystarczyło , aby potem zostać misjonarzem. Sam zaczął czytać Pismo Święte, żywoty świętych , uczył się nawet tekstów na pamięć, jednak głównym zajęciem Dominika Berberii był wypas owiec. Już w mając 14 lat Bóg udzielił mu łaski modlitwy kontemplacyjnej i odtąd poczęło go trawić pragnienie nawracania niewierzących i troski o inną owczarnię, owczarnie Chrystusa. ")]),_vm._v(" "),_c('p',[_vm._v("\n                Dorastając przechodził także kryzys wiary, czytywał książki o wielkich wyprawach, o bohaterach starożytnej Grecji, więc pobożność religijna wydawała mu się małostkowa i niepotrzebna. Brakło także rady mądrej osoby, kierownika duchowego, kogoś kto mógłby mu wiele wytłumaczyć.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Po raz pierwszy spotkał pasjonistów przypadkiem w Vetralla, gdy miał 15 lat. Przyszły czasy napoleońskie, wiele zakonów zostało likwidowanych, także nasi ojcowie musieli opuszczać klasztory. Jeden z nich , o. Giuseppe, schronił się w domu rodzinnym w sąsiedztwie zagrody, gdzie mieszkał nasz chłopak. Doszło do spotkań,  skończyło się na przyjaźni i czytaniu wielu książek z biblioteki Pasjonistów. O. Giuseppe został jego kierownikiem duchowym, uczył go modlitwy, medytacji o Męce Chrystusa, Dominik zaczął regularnie uczęszczać do spowiedzi  i komunii Św. przyszły jednak chwile trudne. Napoleon szykował się do wojny z Rosją, więc potrzebował nowych żołnierzy. Dla Dominika był to wiek poborowy, więc trwożąc się i bojąc silnych mrozów moskiewskich czekał na losowanie poborowych wśród wieśniaków włoskich. Wówczas to nawiedził go dziwny sen. Ujrzał swą matkę , która przekonała go  do ufności Bogu i zachęciła do zapisania się do Bractwa Różańcowego. Jeśli to uczyni, wyciągnie taki numer w losowaniu by uniknąć poboru. Tak też uczynił. Na dzień przed losowaniem uklęknął  jak zwykle i zaczął się modlić , rozmyślał o Męce Pańskiej. W czasie tej długiej modlitwy przyszło natchnienie - by złożyć warunkowy ślub- jeśli uniknie  poboru i jeśli kasata zostanie zniesiona, wstąpi do Pasjonistów. Istotnie modlitwa Dominika została wysłuchana Młody Barberi wyciągnął jeden z najwyższych numerów i zwolniony z poboru.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Przyszła kolejna próba - mając 20 lat zakochał się w dziewczynie! tym bardziej ,iż wujostwo, licząc na oddanie siostrzeńcowi gospodarstwa, razem z proboszczem i z jego polecenia wybrali tę dziewczynę na przyszłą żonę Dominika i gospodynię. Dominik istotnie kochał prostym sercem uroczą i pracowitą dziewuszkę, ale co ze złożonym przyrzeczeniem ? Łudził się faktem, że kasacja zakonów nie została odwołana. Zarzucił modlitwę myślną, idąc jednak na randki często odmawiał różaniec.  Myślał o ożenku. Ojciec Giuseppe , pasjonista nakłaniał do zakonu , inni spowiednicy, których się radził,  opowiedzieli się za małżeństwem. Przyszła rozterka, niepokój . czy było coś złego w miłości do dziewczyny o czystym sercu? Po jakimś czasie wpadł w chorobę tak ciężką, że przyjął namaszczenie chorych. To był czas oczyszczenia i wyrzutów sumienia. Ujrzał siebie w modlitwie przed sądem ostatecznym, doznał trwogi, gdyż wiedział , ze nie wykorzystał wszystkiego, co Bóg mu oferował. Usłyszał głos , wezwanie do wybrania innej drogi. W widzeniu ukazał mu się Maryja, która ręczyła za Dominika przed swym Synem.\n            ")]),_vm._v(" "),_c('p',[_vm._v("\n                Od tego czasu zaczął się więcej modlić, choć targany ciągle wątpliwościami , wychodził powoli z etapu wątpliwości i rozterki. Ponownie zaczął przystępować regularnie do Komunii Św. , czując jak napełnia go siłą. Ponownie przyszedł  glos powołania, coraz bardziej wyraźny i słyszalny w sercu. Musiał zerwać zaręczyny, dziewczyna przyjęła te wiadomość z wielkim smutkiem i z wielką godnością. Przez jakiś czas jeszcze przebywał w gospodarstwie ,  oczekując na legalizację zakonów. Jeden z Pasjonistów zaproponował mu , by został świeckim bratem w Zgromadzeniu, a potem przyjętym do zakonu.  W 1813 r. Dominik Berberii ponownie otrzymał wizje wewnętrzne. Bóg objawił mu przyszłe zadania, których miałby się podjąć jako pasjonista -  głoszenie Ewangelii  zbłąkanym owieczkom.  Był to decydujący moment. Poddał się całkowicie woli bożej - nie ukończywszy szkoły, zaczął sam uczyć się łaciny, doznawał przeżyć mistycznych i tęsknił za życiem zakonnym.  Rok 1813 to koniec prześladowań Kościoła. Dominik najpierw jako brat świecki, potem  nowicjusz rozpoczął swą długą drogę w zakonie Pasjonistów. Nadrobił naukę, odbył studia, został wykładowcą filozofii, opiekunem studentów aż w końcu  jako kapłan został wysłany do Anglii , kraju wrogiego stolicy Apostolskiej. Jego działalność przyniosła plon obfity, przyjął do kościoła Rzymskokatolickiego wielu anglikanów, m.in. Johna H. Newmana, późniejszego kardynała a także do zakonu Georgea Spencera, który był przodkiem słynnej w XX w. księżnej Diany. Dzisiaj do dziedzictwa Dominika Barberi przyznają się pasjoniści w Belgii, Francji, Anglii i Holandii.  Papież Paweł VI beatyfikował Go 27 października 1963 roku.\n            ")])])])])}]
